/**
 * 
 */
export default class SpecialBaseRenderer {
  /**
   * 
   * @param {object} element - Element 
   * @param {openBSE~Options} options - 
   * @param {object} elementSize - 
   */
  constructor(element, options, elementSize) {
      if (new.target === SpecialBaseRenderer) {
          throw new SyntaxError();
      }

      init(); //

      /**
       * 
       * @private @type {boolean}
       */
      let _hide = false;

      /**
       * 
       * @private @type {number}
       */
      let _opacity = 0.0;

      /**
       * 
       * @abstract
       */
      this.cleanScreen = function () {
          throw new SyntaxError();
      }

      /**
       * 
       */
      this.hide = function () {
          _hide = true;
          element.style.visibility = 'hidden';
      }

      /**
       * 
       */
      this.show = function () {
          _hide = false;
          element.style.visibility = '';
      }

      /**
       * 
       */
      this.setOpacity = _setOpacity;

      /**
       * 
       */
      function _setOpacity() {
          if (options.opacity === 1) element.style.opacity = '';
          else element.style.opacity = options.opacity;
      }

      /**
       * 
       * @returns {number}  0.0  1.00.0 1.0 
       */
      this.getOpacity = () => _opacity;

      /**
       * 
       * @returns {boolean} 
       * @description 
       */
      this.getVisibility = () => !_hide;

      /**
       * 
       * @abstract
       */
      this.draw = function () {
          throw new SyntaxError();
      }

      /**
       *  
       * @abstract
       * @param {object} realTimeBulletScreen - 
       */
      this.refresh = function (oldStyle, realTimeBulletScreen) {
        throw new SyntaxError();
      }

      /**
       * 
       * @abstract
       * @param {object} realTimeBulletScreen - 
       */
      this.creat = function (realTimeBulletScreen) {
          throw new SyntaxError();
      }

      /**
       * 
       * @abstract
       * @param {object} realTimeBulletScreen - 
       */
      this.delete = function (realTimeBulletScreen) {
          throw new SyntaxError();
      }

      /**
      * 
      * @function
      */
      this.setSize = setSize;

      /**
       * 
       * @private
       */
      function setSize() {
          element.style.width = `${elementSize.width}px`;
          element.style.height = `${elementSize.height}px`;
          if (options.scaling != 1) {
              element.style.transform =
                  element.style.webkitTransform =
                  element.style.msTransform = `scale(${options.scaling},${options.scaling})`;
              element.style.transformOrigin =
                  element.style.webkitTransformOrigin =
                  element.style.msTransformOrigin = `left top`;
          } else {
              element.style.transform =
                  element.style.webkitTransform =
                  element.style.msTransform =
                  element.style.transformOrigin =
                  element.style.webkitTransformOrigin =
                  element.style.msTransformOrigin = '';
          }
      }

      /**
       * 
       * @private
       */
      function init() {
          setSize();
          _setOpacity();
          element.style.position = 'relative';
      }
  }
}

