import Resources from '../lib/resources'

/**
 * 
 * @private @constant
 */
const RENDERERS = {
    /**
     * CSS3 
     * @private @readonly
     */
    css3: {
        general: require('./generalCss3Renderer').default,
        special: require('./specialCss3Renderer').default
    },
    /**
     * SVG 
     * @private @readonly
     */
    svg: {
        general: require('./generalSvgRenderer').default
    },
    /**
     * WebGL 
     * @private @readonly
     */
    webgl: {
        general: require('./generalWebglRenderer').default
    },
    /**
     * Canvas 2D 
     * @private @readonly
     */
    canvas: {
        general: require('./generalCanvasRenderer').default,
        special: require('./specialCanvasRenderer').default
    }
}

/**
 * 
 */
export default class RenderersFactory {
    /**
     * 
     * @param {object} element - Element 
     * @param {openBSE~Options} options - 
     * @param {object} elementSize - 
     * @param {Event} eventTrigger - 
     */
    constructor(element, options, elementSize, eventTrigger) {
        /**
         * 
         * @param {string} renderMode - 
         * @param {string} engineType - 
         * @returns {BaseRenderer} 
         * @throws {openBSE.BrowserNotSupportError} 
         * @throws {TypeError}  MDN [TypeError]{@link https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/TypeError} 
         */
        this.getRenderer = function (renderMode, engineType) {
            let renderer = RENDERERS[renderMode][engineType];
            if (typeof (renderer) === 'undefined') throw new TypeError(Resources.RENDER_MODE_ERROR.fillData({ renderMode: renderMode }));
            return new renderer(element, options, elementSize, eventTrigger);
        },
        /**
         * 
         * @param {string} renderMode - 
         * @returns {BaseRenderer} 
         * @throws {openBSE.BrowserNotSupportError} 
         * @throws {TypeError}  MDN [TypeError]{@link https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/TypeError} 
         */
        this.getGeneralRenderer = (renderMode) => this.getRenderer(renderMode, 'general');
        /**
         * 
         * @param {string} renderMode - 
         * @returns {BaseRenderer} 
         * @throws {openBSE.BrowserNotSupportError} 
         * @throws {TypeError}  MDN [TypeError]{@link https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/TypeError} 
         */
        this.getSpecialRenderer = (renderMode) => this.getRenderer(renderMode, 'special');
    }
}

